#top {
  min-height: 101vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#top h1 {
  color: #5A5A5A;
  font-family: "Baskerville Old Face";
  font-size: 42pt;
  text-align: center;
  position: relative;
  margin-bottom: 0.5em;
}

.circles {
  text-align: center;
  position: relative;
  display: flex;
  justify-content: center;
}

#projectsSection {
    height: auto;
    padding-bottom: 4em;
    padding-top: 4em;
    text-align: center;
    background-color: #5A5A5A;
}
#projectsSection h2 {
    color: #F9F9F9;
    position: relative;
    text-align: center;
    font-size: 36pt;
    background: transparent;
    z-index: 1;
}
#ddpList {
    background-color: transparent;
    margin-top: 4em;
    padding: 0;
    width: 100%;
}
#ddpList li {
    display: inline-block;
    margin: 1em;
    font-size: 24pt;
    font-weight: bolder;
    color: #F9F9F9;
    background-color: inherit;
    transition: 0.3s all;
}
#ddpList li:hover {
    cursor: pointer;
    color: #6E9E69;
}
.active {
    text-decoration: underline;
}
.design, .development, .programming {
    background-color: #5A5A5A;
}

#about {
    margin-top: 3em;
    margin-left: 15vw;
    width: 60vw;
    padding-bottom: 5em;
}
#about p {
    color: #5A5A5A;
    font-size: 16pt;
    margin-bottom: 2em;
    margin-left: 2em;
}
#about h2 {
    font-size: 26pt;
    margin-bottom: 1em;
}
#about h3, #resumes h3 {
    font-size: 18pt;
    margin-bottom: 1em;
}
#about h2, #about h3, #resumes h3 {
    color: #6E9E69;
}
#contact {
    margin-bottom: 5em;
}
#resumes {
    max-width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.icons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

/* begin media queries */
@media screen and (max-width: 1100px) {
    #resumes {
        padding-bottom: 10em;
    }
    #triag {
        bottom: 0;
    }

    #projects h2 {
        top: 1.2em;
    }
}

@media screen and (max-width: 600px) {
    /* remove buttons (yikes) */
    .circles {
        display: none;
    }
    
    #about p {
        margin-left: 0;
    }

    #projects h2 {
        font-size: 18pt;
    }
}