.circle {
    padding: 3.75em;
    border-radius: 50%;
    border: none;
    background-color: #6E9E69;
    box-shadow: 3px 3px #5A5A5A;
}
.circle:hover {
    box-shadow: -1px -1px #858585;
    background-color: #4A6D47;
    cursor: pointer;
    position: relative;
    left: 3%;
}
.circle:hover + p {
    color: black;
}
.circleContainer p {
    margin-top: 0.5em;
    color: #5A5A5A;
    font-size: 18pt;
}
.circleContainer {
    width: 15%;
}

#about {
    margin-top: 3em;
    margin-left: 15vw;
    width: 60vw;
}
#about a {
    text-decoration: none;
    color: #5A5A5A;
}
#about a:hover {
    color: #6E9E69;
}
#about p {
    color: #5A5A5A;
    font-size: 16pt;
    margin-bottom: 2em;
    margin-left: 2em;
}
#about h2 {
    font-size: 26pt;
    margin-bottom: 1em;
}
#about h3, #resumes h3 {
    font-size: 18pt;
    margin-bottom: 1em;
}
#about h2, #about h3, #resumes h3 {
    color: #6E9E69;
}
#contact {
    margin-bottom: 5em;
}

#resumes {
    max-width: 100%;
    height: 65em;
}